import { InputLabel, MenuItem, Select, SelectChangeEvent } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackForm, FormControlStyled } from "./FeedbackMode.styles"
import { SearchIntent, searchIntentOptions } from "./feedback.types"

export default function SearchIntentForm() {
  const { searchIntent, setSearchIntent } = useFeedbackContext()

  const handleChange = (event: SelectChangeEvent) => {
    setSearchIntent(event.target.value as SearchIntent)
  }

  const options = searchIntentOptions.map(option => (
    <MenuItem key={option.value} value={option.value}>
      {option.label}
    </MenuItem>
  ))

  return (
    <FeedbackForm>
      <FormControlStyled>
        <InputLabel id="search-intention-label">Search Intention</InputLabel>
        <Select
          disabled
          data-testid="search-intention-profile"
          value={searchIntent}
          label="search-intention"
          onChange={handleChange}
          inputProps={{
            "data-testid": "search-intention-options-id",
          }}
        >
          {options}
        </Select>
      </FormControlStyled>
    </FeedbackForm>
  )
}
